<template>
<TabContent>
<v-container fill-height v-if="!error">
<v-row class="fill-height" align-content="center" justify="center">
  <v-col cols="6">
    <v-card>
      <v-card-text>
    <v-progress-linear
      color="deep-purple accent-4"
      indeterminate
      rounded
      height="6"
    />
      </v-card-text>
    </v-card>
  </v-col>
</v-row>
</v-container>
<v-overlay v-else :z-index="1">
  <v-card>
    <v-card-title class="headline">Произошла ошибка</v-card-title>
    <v-card-text>{{error.message}}</v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="green darken-1" text>
        Обновить
      </v-btn>
    </v-card-actions>
  </v-card>
</v-overlay>
</TabContent>
</template>

<script>
import TabContent from '@/render/components/Common/TabContent.vue';

export default {
  components: { TabContent },
  props: ['error', 'tab'],

  watch: {
    error() {
      if (this.error && this.tab) {
        this.tab.color = 'red';
      }
    }
  }
};
</script>
